import React, { useMemo } from "react";
import { useSelector } from "react-redux";
import DataTable from "../../formElements/DataTable";
import { useNavigate } from "react-router-dom";
import { FaEdit } from "react-icons/fa";
import { FaLongArrowAltLeft } from "react-icons/fa";
import { items } from "../../../redux/slices/Items_Slice";
import { itemGroup } from "../../../redux/slices/ItemGroupSlice";

const List = () => {
  const itemGroups = useSelector(itemGroup);
  const Items = useSelector(items);
  const navigate = useNavigate();
  const modifiedItems = [];

  useMemo(() => {
    const itemGrpoupMap = new Map();
    itemGroups.forEach((grp) => {
      itemGrpoupMap.set(grp.id, grp.Group);
    });

    Items.forEach((item) => {
      if (itemGrpoupMap.has(item.gpid)) {
        modifiedItems.push({
          ...item,
          Group: itemGrpoupMap.get(item.gpid),
        });
      }
    });
  }, [itemGroups, Items]);
  const data = modifiedItems.map((Cat) => {
    return {
      id: Cat.id,
      Group: Cat.Group,
      item: Cat.item,
      price: Cat.price,
      inactive: Cat.inactive === 1 ? "Yes" : "No",
    };
  });

  const columns = useMemo(
    () => [
      {
        Header: "Item",
        accessor: "item",
      },
      {
        Header: "Item Group",
        accessor: "Group",
      },
      {
        Header: "Price",
        accessor: "price",
      },
      {
        Header: "Inactive",
        accessor: "inactive",
      },
      {
        Header: "Edit",
        accessor: "id", // Assuming 'id' is the unique identifier for your rows
        Cell: ({ row }) => (
          <button
            onClick={() => {
              navigate("../", { state: { itemid: row.original.id } });
            }}
          >
            <FaEdit className="hover:text-orange-500" />
          </button>
        ),
      },
    ],
    []
  );

  return (
    <>
      <div className="px-7 py-1 overflow-auto flex flex-col   h-[calc(100vh-70px)]">
        <div className="inline-flex items-center">
          <span
            className=" inline-block p-2  text-slate-500 cursor-pointer hover:bg-slate-700 hover:text-slate-300 hover:rounded-lg "
            onClick={() => navigate("../")}
          >
            <FaLongArrowAltLeft></FaLongArrowAltLeft>
          </span>
          <span className=" text-slate-300 text-sm">Back</span>
        </div>
        {modifiedItems.length > 0 ? (
          <DataTable columns={columns} data={data} />
        ) : (
          <p>No Items Found</p>
        )}
      </div>
    </>
  );
};

export default List;
