import React from "react";
import {
  useTable,
  usePagination,
  useSortBy,
  useGlobalFilter,
} from "react-table";
import "./DataTable.css";
import { BiSolidUpArrow, BiSolidDownArrow } from "react-icons/bi";

function DataTable({ columns, data }) {
  const {
    getTableProps,
    getTableBodyProps,
    headerGroups,
    page,
    prepareRow,
    state: { pageIndex, globalFilter },

    setGlobalFilter,

    canPreviousPage,
    canNextPage,
    nextPage,
    previousPage,
    pageOptions,
  } = useTable(
    {
      columns,
      data,
      initialState: { pageIndex: 0, pageSize: 10 },
    },
    useGlobalFilter,
    useSortBy,
    usePagination
  );

  return (
    <div className="table-wrapper">
      <input
        className=" float-end outline outline-1 text-gray-400 ps-2 outline-orange-500 focus:text-orange-500 "
        value={globalFilter || ""}
        onChange={(e) => setGlobalFilter(e.target.value)}
        placeholder="Search..."
      />
      <table {...getTableProps()} className="custom-table">
        <thead>
          {headerGroups.map((headerGroup, index) => (
            <tr key={index} {...headerGroup.getHeaderGroupProps()}>
              {headerGroup.headers.map((column, columnIndex) => {
                const { key, ...columnProps } = column.getHeaderProps(
                  column.getSortByToggleProps()
                );
                return (
                  <th
                    key={columnIndex}
                    {...columnProps}
                    className="custom-header"
                  >
                    {column.render("Header")}
                    <span>
                      {column.isSorted ? (
                        column.isSortedDesc ? (
                          <BiSolidDownArrow className=" inline-block text-white ps-2 text-2xl" />
                        ) : (
                          <BiSolidUpArrow className=" inline-block text-white ps-2 text-2xl" />
                        )
                      ) : (
                        ""
                      )}
                    </span>
                  </th>
                );
              })}
            </tr>
          ))}
        </thead>
        <tbody {...getTableBodyProps()}>
          {page.map((row, index) => {
            prepareRow(row);
            return (
              <tr key={index} {...row.getRowProps()} className="custom-row">
                {row.cells.map((cell) => {
                  const { key, ...cellProps } = cell.getCellProps();
                  return (
                    <td key={key} {...cellProps} className="custom-cell">
                      {cell.render("Cell")}
                    </td>
                  );
                })}
              </tr>
            );
          })}
        </tbody>
      </table>
      <div className="flex justify-between items-center mt-3">
        <div className="flex gap-3">
          <button
            onClick={() => previousPage()}
            disabled={!canPreviousPage}
            className=" bg-orange-500 w-20 h-8 rounded-md text-white font-semibold hover:bg-orange-400 "
          >
            Previous
          </button>
          <button
            onClick={() => nextPage()}
            disabled={!canNextPage}
            className=" bg-orange-500 w-20 h-8 rounded-md text-white font-semibold hover:bg-orange-400"
          >
            Next
          </button>
        </div>

        <div className=" text-gray-400 text-md font-mono">
          Page{" "}
          <em className="text-orange-500">
            {pageIndex + 1} of {pageOptions.length}
          </em>
        </div>
      </div>
    </div>
  );
}

export default DataTable;
